<template>
  <v-layout class="container">
    <v-col :class="mainColClass()">
      <h1 :class="headerClass()">
        Social distancing made
        <i><b>simple.</b></i>
      </h1>
      <blockquote :class="quoteClass()">
        Low cost, high accuracy occupancy analytics.
      </blockquote>
      <router-link to="/#contact">
        <v-btn text class="infoButton" elevation="5" color="secondary"
          >Request Info</v-btn
        >
      </router-link>
    </v-col>
    <v-col>
      <v-card class="item consoleImage" elevation="10" :width="imageHeight()">
        <v-img :src="require('../assets/accountPage.png')"></v-img>
      </v-card>
    </v-col>
  </v-layout>
</template>

<script>
export default {
  name: "Main",
  methods: {
    imageHeight() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "200px";
        case "sm":
          return "300px";
        case "md":
          return "400px";
        case "lg":
          return "500px";
        case "xl":
          return "500px";
      }
    },
    headerClass() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "display-2 text-center";
        case "sm":
          return "display-4 text-center";
        case "md":
          return "display-4";
        case "lg":
          return "display-4";
        case "xl":
          return "display-4";
      }
    },
    quoteClass() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "blockquote item quote text-center";
        case "sm":
          return "blockquote item quote text-center";
        case "md":
          return "blockquote item quote";
        case "lg":
          return "blockquote item quote";
        case "xl":
          return "blockquote item quote";
      }
    },
    mainColClass() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "d-flex flex-column align-center";
        case "sm":
          return "d-flex flex-column align-center";
        case "md":
          return "d-flex flex-column align-center";
        case "lg":
          return "d-flex flex-column align-left";
        case "xl":
          return "d-flex flex-column align-left";
      }
    },
  },
};
</script>

